import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    gamePk : 0,
    currentDate : new Date(),
    server: {
      ip: [10,101,26,102],
      port: 8051
    }
  },
  mutations: {
    CHANGE_DATE(state, date) {
      state.currentDate = date;
    },
    UPDATE_IP(state, serverData) {
      while(state.server.ip.length) state.server.ip.pop();
      serverData.ip.forEach(elem => { state.server.ip.push(elem) });
      state.server.port = serverData.port;
    },
    UPDATE_GAME_PK(state, gamePk) {
      console.log('UPDATE_GAME_PK', gamePk);
      state.gamePk = gamePk;
    }
  },
  actions: {
    incrementDate(context, direction = 1) {
      let newDate = new Date();
      newDate.setTime(context.state.currentDate.getTime() + ((24 * 60 * 60 * 1000) * direction));
      context.commit('CHANGE_DATE', newDate);
    }
  },
  getters: {
    ip: state => { return `${state.server.ip.join('.')}:${state.server.port}`; }
  }
})
